
.LeftBar{
    background: linear-gradient(0deg, rgba(2,0,36,1) 0%, rgba(7,7,103,1) 47%, rgba(1,31,37,1) 100%);
    font-weight: bold;
    font-size: large;
    font-family: Verdana, Geneva, Tahoma, sans-serif;
    color:beige;
    flex-direction: column;
    justify-content: space-evenly;
    min-height:100vh;
    display: flex;
    margin:0;
    padding:0;
    flex-wrap:1;
}

.LeftBar > p{
    border-top-width: 2px;
    border-top-color:grey;
}

.LeftBar > img{
    margin-left:25%;
}

.LeftBar > * {
    flex:1;
}